.renderTable {
    // padding-left:100px;
    max-width:50em;
    // margin: auto !important;
    &.noMarginTable {
        margin: 0 auto;
    }
    &.withMarginTable {
        margin: 1.5em auto;
    }
    th,td {
        // width:auto;
        padding: 0 !important;
        &.align-center {
            text-align:center;
            span {
                text-align:center;
            }
        }
        &.align-left {
            text-align:left;
            span {
                text-align:left;
            }
        }
    }
    td {
        padding: 0 !important;
        div {
            position:relative; 
            number {
                position: absolute;
                left: -2em;
                font-style: normal;
            }
        }
    }
}

.renderContract-lists {
    number {
        position: absolute;
        left: 0;
    }
}

.downloadProposal {
    font-size: 1.15em;
    color: #246106;
    margin-left: 10px;
    font-style: normal;
    font-weight: 600;
    svg {
        padding-right: 2.5px;
        transform: translateX(-5px);
    }
}